<template>
	<view>
		<!-- 删除确认框 -->
		<u-modal :show="show" width="500rpx" confirmColor="white" :showCancelButton="true" :closeOnClickOverlay="true" content='确认删除此收藏？' @confirm="sureDel" @cancel="cancelDel"></u-modal>
		<!-- 选项卡 -->
		<u-tabs itemStyle="height:88rpx;font-weight:600" lineColor="#7f4c12" lineWidth="40" :current="current" :list="list" @click="click"></u-tabs>
		<!-- 显示内容 -->
		<view class="content">
			<!-- 长难句 -->
			<view v-if="current==0">
				<view v-if="data.lds.length<1" class="h5 center">{{tips}}</view>
				<u-swipe-action 
				v-for="(item,index) in data.lds" 
				:key="item.id" 
				>
					<u-swipe-action-item
					:options="options1"
					@click="clicks(item,index)"
					>
					 	<view class="words" v-html="item.title" @click="toLds(item)"></view>
					 	<view class="teacher">--{{item.from}}</view>
					</u-swipe-action-item>
			    </u-swipe-action>
			</view>
			<!-- 词汇 -->
			<view v-else-if="current==1">
				<view v-if="data.word.length<1" class="h5 center">{{tips}}</view>
				<view v-for="item in data.word" :key="item.id" class="express_content">
					<view>
						<view class="h5">{{item.word}}</view>
						<view class="h6">{{item.explain}}</view>
					</view>
					<u-icon name="star-fill" color="#ffd75c" size="38rpx" @click="isStar(item)"></u-icon>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import { collectionList, collection } from '@/common/http.api.js';
	export default {
		data() {
			return {
				token:'',
				show: false,
				data:{},
				item:'',
				index: 0,
				options1: [{
					text: '删除',
					style: {
						backgroundColor: '#ffd75c'
					}
				}],
				list: [{
					name: '简单句',
				}, {
					name: '主从复合句',
				}, {
					name: '并列复合句',
				}, {
					name: '182句真题练习',
				}],
				current: 0,
				tips:"暂无收藏"
			}
		},
		onLoad() {
			this.token = uni.getStorageSync('token')
			collectionList({ token:this.token }).then(res => {
				console.log(res)
				this.data = res.data.data
				if(this.data.lds.length>=1){
					this.list[0].name = "长难句"+"("+this.data.lds.length+")"
				}
				if(this.data.word.length>=1){
					this.list[1].name = "词汇"+"("+this.data.word.length+")"
				}
			}).catch(err=>{
				console.log(err)
			})
			const params = {
				page: 1
			}
		},
		methods: {
			async sureDel() {
				this.show = false
				this.data.lds.splice(this.index,1)
				setTimeout(()=>{
				  this.$u.toast("删除成功"); 
				},1000)
				
				await collection({ token:this.token ,id:this.item.id, type:this.item.type }).then(res => {
					console.log(res)
				}).catch(err=>{
					console.log(err)
				})
				collectionList({ token:this.token }).then(res => {
				console.log(res)
				this.data = res.data.data
				if(this.data.lds.length>=1){
					this.list[0].name = "长难句"+"("+this.data.lds.length+")"
				}
				if(this.data.word.length>=1){
					this.list[1].name = "词汇"+"("+this.data.word.length+")"
				}
				}).catch(err=>{
					console.log(err)
				})
			},
			cancelDel() {
				this.show = false
			},
			clicks(item,index){
				this.item = item
				this.index = index
				this.show = true
				console.log(this.show)
			},
			open(index) {
				// 先将正在被操作的swipeAction标记为打开状态，否则由于props的特性限制，
				// 原本为'false'，再次设置为'false'会无效
				this.data.lds[index].show = true;
				console.log(this.data.lds[index].show)
				this.data.lds.map((val, idx) => {
					if(index != idx) this.data.lds[idx].show = false;
				})
			},
			click(item) {
				this.current = item.index
				console.log("我")
			},
			isStar(e) {
				collection({ token:this.token ,id:e.id, type:'word' }).then(res => {
				console.log(res)
				}).catch(err=>{
					console.log(err)
				})
				e.is_collection == 0 ? e.is_collection = 1 : e.is_collection = 0
			},
			toLds(e){
				uni.navigateTo({
					url: "../index/wordList?sentence_id="+e.id
				})
			}
		}
	}
</script>

<style lang="less">
	.u-swipe-action-item__right{
		width: 100rpx !important;
		text-align: center !important;
		padding: 0 10rpx !important;
	}
	.u-swipe-action-item__right__button__wrapper{
		padding: 0 20rpx !important;
		margin-left: 10rpx !important;
	}
	.words {
		margin-bottom: 20rpx;
		line-height: 48rpx;
	}

	.teacher {
		color: #999;
		font-size: 24rpx;
		margin-bottom: 20rpx;
		margin-bottom: 40rpx;
	}
		
	.content {
		padding: 40rpx;
		margin-bottom: 40rpx;
		.express_content{
			display: flex;
			justify-content: space-between;
		}
		.h5 {
			font-size: 32rpx;
			font-weight: 500;
			line-height: 48rpx;
		}

		.h6 {
			font-size: 24rpx;
			line-height: 36rpx;
			color: #999;
			font-weight: 400;
			margin-bottom: 20rpx;
		}
	}
</style>
